body {
  background: #7BAFD4; /*carolina blue*/
  font-family: Avenir, 'Helvetica Neue', sans-serif;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.slip-swiping-container {
  overflow: hidden;
}

.App .content {
  display: -ms-flexbox;
  display: flex;
}

.App .schedule-validators-container {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
}

.App .schedule-container, .App .validators-container {
  width: 100%;
}

.App .schedule-container {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: 40px;
}

.App .validators-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: 40px;
  padding: 20px;
  max-width: 150px;
}

.App .search-bar-container {
  padding: 10px;
  width: 100%;
  -ms-flex-negative: 4;
      flex-shrink: 4;
}

@media (max-width: 983px) {
  .App .schedule-validators-container {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .App .schedule-container {
    margin-top: 0px;
  }
  .App .validators-container {
    margin-top: unset;
    max-width: unset;
    width: unset;
  }
}

@media (max-width: 845px) {
  .App .content {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .App .search-bar-container {
    width: unset;
  }
}


